<template>
<div class='home'>
          <v-text-field
          @click:append="addTask"
          v-model="newTaskTitle"
          @keyup.enter="addTask"
          class="pa-3"
            outlined
            label="Add Task"
            append-icon="mdi-plus"
            hide-details
            clearable
          ></v-text-field>
    <v-list
    class="pt-0"
      flat
    >
     <div
     v-for="task in tasks"
     :key="task.id"
     >
        <v-list-item
        @click="doneTask(task.id)"
        :class="{ 'blue lighten-5' : task.done }"
        >
          <template v-slot:default>
            <v-list-item-action>
              <v-checkbox
              :input-value="task.done"
              color="primary"
              ></v-checkbox>
            </v-list-item-action>
            <v-list-item-content
            :class="{ 'text-decoration-line-through' : task.done }"
            >
              <v-list-item-title>{{ task.title }}</v-list-item-title>
            </v-list-item-content>

        <v-list-item-action>
          <v-btn
          @click.stop="deleteTask(task.id)"
          icon>
            <v-icon color="primary">mdi-trash-can</v-icon>
          </v-btn>
        </v-list-item-action>

          </template>
        </v-list-item>
        <v-divider></v-divider>
      </div>
    </v-list>
  </div>
</template>

<script>

  export default {
    // name: 'Home',
    data () {
      return{
        newTaskTitle: '',
        tasks: [
        {
          id: 1,
          title: '起床',
          done: true
        },
        {
          id: 2,
          title: '洗澡',
          done: false
        },
        {
          id: 3,
          title: '睡覺',
          done: false
        },
        ]
      }
    },
    methods: {
      addTask () {
        let newTask = {
          id: Date.now(),
          title: this.newTaskTitle,
          done:false
        }
        if (newTask.title.length == 0 || newTask.title.replace(/(^\s*)|(\s*$)/g, "").length ==0) {
          alert('不能為空')
        } else {
        this.tasks.push(newTask)
        this.newTaskTitle=''}//為了清框輸入框
        // localStorage.setItem(this.tasks, newTask);
      },
      doneTask (id) {
        // console.log('測試id:', id)
        let task = this.tasks.filter( task => task.id === id)[0]//此方法是為了修改tasks內的done變數，將其改為相反的布爾值
        task.done = !task.done
      },
      deleteTask (id) {
        // id傳進來的值與task.id相比，是相同的就把tasks.id相同的刪掉。
        this.tasks = this.tasks.filter( task => task.id !== id)//  filter 方法使用目的是移除所有的 ”false“ 类型元素
        
        // this.tasks = this.tasks.filter((task) => {
        //   console.log('task id:',task.id)
        //   console.log('id:', id)
        //   let a = task.id !== id
        //   console.log(a)
        // })

        // 原本函式寫法
        // elements.map(function(element) {
        //    return element.length;
        //       });
      }
    }
  }
</script>
